:root {
  --light-background-color: #ffffff;
  --light-text-color: #22343d;
  --light-accordion-color: #e7e7e7;

  // -------------Dark Theme----------------

  --dark-background-color: #1f2e35;
  --dark-text-color: #ffffff;
  --dark-accordion-color: #474747;

  // -------------Established Colors--------
  --logo-text-color: #173a56;

  --green-color: #02897a;
  --blue-color: #4d8dff;
  --purple-color: #740a76;
  --red-color: #f03e3d;
  --yellow-color: #ff9900;
  --white-color: #ffffff;

  --border-color: #dedede;
  --color-for-mobile-form: #bcd0e5;

  --form-color: #f5f5f5;
  --form-border-color: #e8e8e8;

  --background-color-for-sections: #f5f5f5;
  --background-color-for-business: #ffefef;
  --background-color-for-enterprise: #f2f6ff;
  --background-color-for-socials: #edfffc;
  --background-color-for-features: #f4f5f7;

  //-----------------animation---------------

  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --time-of-animation: 350ms;
}
